*{
    margin: 0;
    padding: 0;
    --orange-color:rgb(255, 81, 78);
    --WhitishOrange-color: rgb(249, 121, 118);
    --WhitishYellow-color: rgb(252, 171, 109);
    --grey-color:rgb(31, 34, 51);
}

body, html {
    margin: 0;
    padding: 0;
    overflow-x: hidden; /* Prevent horizontal scrolling */
}


.profile-container {
    display: flex;
    align-items: center;       /* vertically align text & image */
    justify-content: center;   /* center the whole group */
    gap: 250px;                /* space between text and image */
    padding: 80px 20px;
    background: var(--grey-color);
    background-attachment: fixed;
    width: 100%;
    /* max-width: 1200px;         limit max width for large screens */
    margin: 0 auto;            /* center container on screen */
    box-sizing: border-box;
}


.profile-content {
    display: flex;
    flex-direction: column; /* stack inside text */
    align-items: flex-start; /* align text to left (looks better) */
    justify-content: center;
    color: white;
    max-width: 500px;
    width: 100%;
    text-align: left;        /* left align text */
}



    
.social-media{
    margin-bottom: 15px;
    font-size: 25px;
}
.social-media a{
    color: white;
    padding: 0px 15px;
}

.profile-detail{
    text-align: center;
}

.profile-name{
    font-size: 20px;
}

.profile-name .name{
    color: var(--WhitishOrange-color);
    font-size: 30px;
}

.profile-role{
    font-size: 35px;
    margin: 14px 0px 34px 0px;
}

.profile-role-tagline{
    font-size: 17px;
}

.profile-button{
    margin-top: 35px;
}

.button{
    border: none;
    background: none;
    font-size: 17px;
    font-weight: 550;
    margin-left: 15px;
    padding: 10px 20px 10px 20px;
    background-color: var(--orange-color);
    border-radius: 20px;
    cursor: pointer;
    transition: 0.3s;
}

.resume:hover{
    background-color: var(--WhitishOrange-color);
}

.hire-me{
    border: 2px solid white;
    color: white;
    background-color: transparent;
    background: linear-gradient(to right, var(--orange-color) 50%, transparent 50%);
    background-size: 200% 100%;
    background-position: right bottom;
    transition: all .3s ease-out;
}

.hire-me:hover{
    background-position: left bottom;
    border-color: var(--orange-color);
    color: black;
}

.profile-img {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 350px;
    width: 350px;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 0px 2px 0px 0px white;
}


.picture-background{
    height: 92%;
    width: 92%;
    background-image: url(../../Assets/Home/profile-pic.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;
    z-index: 2;
}

.picture-background:hover{
    transform: scale(1.07);
    transition: 1s ease-out;
}

.svg-img {
    display: block;
    width: 100%;
    margin: 0; /* Remove gap */

}


/* Heading of all section */

.title-main{
    height: 100vh;
}
.title{
    text-align: center;
    padding-top: 80px;
}

.title h1{
    display: inline-block;
    border-bottom: 2px solid var(--orange-color);
    border-radius: 10px;
    width: 200px;
    padding-bottom: 5px;
}

.title p{
    font-size: 14px;
}


/* svg{
    height: 400px;
} */

@media screen and (max-width: 360px) {
    .profile-container{
        flex-direction: column-reverse;
        padding-top: 70px;
        gap: 0px;
    }

    .profile-img{
        height: 250px;
        width: 250px;
        /* margin-bottom: 20px; */
    }

    .profile-content{
        width: 90%;
        /* margin-top: 20px; */
    }
    
    .profile-name{
        font-size: 16px;
    }

    .profile-name .name{
        font-size: 22px;
    }

    .profile-role{
        font-size: 22px;
    }

    .profile-role-tagline{
        font-size: 14px;
    }

    .social-media a{
        font-size: 20px;
        padding: 0px 10px;
    }
}

@media screen and (min-width: 361px) and (max-width: 768px) {
    .profile-container{
        flex-direction: column-reverse;
        padding-top: 70px;
        gap: 0px;
    }

    .profile-img{
        height: 300px;
        width: 300px;
        margin-bottom: 20px;
    }

    .profile-content{
        width: 80%;
        margin-top: 20px;
    }

    .profile-name{
        font-size: 18px;
    }

    .profile-name .name{
        font-size: 26px;
    }

    .profile-role{
        font-size: 28px;
    }

    .profile-role-tagline{
        font-size: 16px;
    }

    .social-media a{
        font-size: 22px;
        padding: 0px 12px;
    }

    .button{
        font-size: 16px;
        padding: 8px 18px;
    }
}

@media screen and (max-width: 768px) {
    .picture-background:hover{
        transform: scale(1.05);
    }
}


@media screen and (max-width: 768px) {
    .svg-img{
        margin-top: 0px;
    }
}


